<script>
import axios from 'axios';
import * as XLSX from 'xlsx';
export default {
  data() {
    return {
      acuntUsers: [], // 儲存 acunt_user 資料
      selectedBook: null,
      books: [], // 儲存 acunt_books 資料
      editingBook: null,
      editedBook: {
        in_and_out:'',
        amount: '',
        dates: '',
        class:'',
        item:'',
        remark:''
      },
      newBook: { 
        in_and_out: '',
        amount: '',
        dates: '',
        class: '',
        item: '',
        remark: ''
      },
      newUser: {
        acunt_book_name: ''
      },
      deletingUser: null,
      deletingBook: null // 用於保存當前要刪除的 book_id

      
    };
  },
  created() {
    this.fetchAcuntUsers();
  },
  methods: {
    fetchAcuntUsers() {
      axios.get('http://127.0.0.1:5000/api/acunt_user')
        .then(response => {
          this.acuntUsers = response.data;
        })
        .catch(error => {
          console.error('Error fetching acunt users:', error);
        });
    },
    fetchBooks() {
      if (this.selectedBook) {
        axios.get(`http://127.0.0.1:5000/api/acunt_books/${this.selectedBook.acunt_book_name}`)
          .then(response => {
            this.books = response.data;
          })
          .catch(error => {
            console.error('Error fetching books:', error);
          });
      }
    },
    addNewUser() {
      axios.post('http://127.0.0.1:5000/api/acunt_user', this.newUser, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          console.log('New user added:', response.data);
          this.newUser.acunt_book_name = '';
          this.fetchAcuntUsers();
        })
        .catch(error => {
          console.error('Error adding new user:', error);
        });
    },
    confirmDeleteUser(acunt_book_name) {
      this.deletingUser = acunt_book_name;
    },
    deleteUser() {
      axios.delete(`http://127.0.0.1:5000/api/acunt_user/${this.deletingUser}`)
        .then(response => {
          console.log('User deleted:', response.data);
          this.fetchAcuntUsers();
          this.deletingUser = null;
        })
        .catch(error => {
          console.error('Error deleting user:', error);
        });
    },
    selectUser(user) {
      this.selectedBook = user;
      this.newBook.acunt_book_name = user.acunt_book_name;
      this.fetchBooks(); // 加載選擇的帳本記錄
    },
    addNewBook() {
      const bookData = {
        ...this.newBook,
        acunt_book_name: this.selectedBook.acunt_book_name
      };
      axios.post('http://127.0.0.1:5000/api/acunt_books', bookData, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          console.log('New book record added:', response.data);
          this.fetchBooks(); // 重新加載帳本記錄
          this.resetNewBook();
        })
        .catch(error => {
          console.error('Error adding new book record:', error);
        });
    },
    confirmDeleteBook(bookId) {
      this.deletingBook = bookId;
    },
    deleteBook() {
      axios.delete(`http://127.0.0.1:5000/api/acunt_books/${this.deletingBook}`)
        .then(response => {
          console.log('Book deleted:', response.data);
          this.fetchBooks(); // 重新加載帳本記錄
          this.deletingBook = null;
        })
        .catch(error => {
          console.error('Error deleting book:', error);
        });
    },
    cancelDeleteBook() {
      this.deletingBook = null;
    },
    deselectBook() {
      this.selectedBook = null;
      this.books = [];
    },
    resetNewBook() {
      this.newBook = {
        in_and_out: '',
        amount: '',
        dates: '',
        class: '',
        item: '',
        remark: ''
      };
    },
    cancelDeleteUser() {
      this.deletingUser = null;
    },
    getInAndOutLabel(inAndOut) {
      return inAndOut === 0 ? '支出' : '收入';
    },
    // 查询特定时间段内帐本记录的方法
    fetchBooksByPeriod() {
      axios.post(`http://127.0.0.1:5000/api/acunt_books/${this.selectedBookName}/period`, {
          start_date: this.start_date,
          end_date: this.end_date
        })
        .then(response => {
          this.books = response.data;
        })
        .catch(error => {
          console.error('Error fetching books by period:', error);
        });
    },
    editBook(book) {
      this.editingBook = book;
      this.editedBook = { ...book, dates: new Date(book.dates).toISOString().substr(0, 10) };
    },
    cancelEdit() {
      this.editingBook = null;
    },
    saveEditedBook() {
      axios.put(`http://127.0.0.1:5000/api/acunt_books/${this.editingBook.book_id}`, this.editedBook)
        .then(response => {
          console.log('Book updated:', response.data);
          this.fetchBooks();
          this.editingBook = null;
        })
        .catch(error => {
          console.error('Error updating book:', error);
        });
    },
    updateAmount(event) {
      const value = event.target.value;
      this.editedBook.amount = Math.floor(value); // 将值转换为整数
    },
    // 匯出帳本資料到 Excel
    exportToExcel() {
      const wb = XLSX.utils.book_new();
      const ws = XLSX.utils.json_to_sheet(this.books);
      XLSX.utils.book_append_sheet(wb, ws, '帳本記錄');
      XLSX.writeFile(wb, `${this.selectedBook.acunt_book_name}_帳本記錄.xlsx`);
    },

    
  }
};
</script>
